.movie__contain{
    width: 250px;
    height: 450px;
    background-color: black;
    display: grid;
    grid-template-rows: 3fr 1fr;
    border-radius: 10px;
    margin-bottom: 5%;
}
.movie__contain:hover{
    scale: 1.2;
    transition: 0.5s;
}
.movie__img{
    width: 100%;
}
.movie__title{
    height: 50%;
    padding: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    border-top: 1px solid gray;
}
.movie__title>a{
    color: rgb(220, 220, 220);
    font-size: 1em;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}