@font-face {
    font-family: 'Giants-Inline';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2307-1@1.1/Giants-Inline.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

.container{
    width: 100%;
    height: 100%;
}
.loading{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
    font-size: 5em;
    background-color: black;
    font-family: 'Giants-Inline';
    color: rgb(120, 34, 34);
}
.content{
    position: relative;
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-rows: 1fr 5f;
    background-color: black;
}
.bkimg{
    width: 100%;
    height: 100%;
    background-image: url("../img/bkground.jpg");
    background-size: cover;
    background-position: center;
    position: absolute;
    opacity: 0.4;
    filter: blur(5px);
}
.header{
    z-index: 9999;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 8%;
}
.header>.header__title{
    font-size: 4em;
    color: rgb(150, 52, 52);
    font-family: 'Giants-Inline';
}

.main{
    z-index: 9999;
    padding: 0 5% 5% 5%;
    margin-bottom: 20%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 3%;
}