@font-face {
    font-family: 'Giants-Inline';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2307-1@1.1/Giants-Inline.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

.container{
    width: 100%;
    height: 100%;
}
.loading{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
    font-size: 5em;
    background-color: black;
    font-family: 'Giants-Inline';
    color: rgb(120, 34, 34);
}
.content{
    position: relative;
    width: 100%;
    min-height: 100vh;
    display: grid;
    grid-template-rows: 1fr 3fr;
    background-color: black;
}
.bkimg{
    width: 100%;
    height: 100%;
    background-image: url("../img/bkground.jpg");
    background-size: cover;
    background-position: center;
    position: absolute;
    opacity: 0.4;
    filter: blur(5px);
}
.header{
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
}
.header>.header__title>a{
    font-size: 4em;
    color: rgb(150, 52, 52);
    font-family: 'Giants-Inline';
}
.main{
    min-height: calc(100vh-header);
    z-index: 9999;
}
.movie__contain{
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.movie__left{
    width: 100%;
    height: 600px;
    max-width: 300px;
}
.movie__img{
    width: 100%;
    height: 100%;
}
.movie__right{
    width: 100%;
    height: 300px;
    max-width: 600px;
    display: grid;
    grid-template-rows: 1fr 3fr;
    padding: 5%;
    margin: 0 5% 0 5%;
    align-items: center;
    background-color: rgb(220, 220, 220);
    margin-bottom: 20%;
}
.movie__title{
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 1.8em;
    font-weight: 800;
    margin-bottom: 5%;
}
.movie__year{
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 1.5em;
    font-weight: 800;
    margin-bottom: 5%;

}
.movie__genres{
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    gap: 10px;
    font-size: 1em;
}
.movie__summary{
    font-family: Arial, Helvetica, sans-serif;
    height: 100%;
    margin-top: 5%;
    align-self: flex-start;
    overflow-y: auto;
    max-height: 200px;
}
.movie__summary::-webkit-scrollbar{
    background-color: black;
}
.movie__summary::-webkit-scrollbar-thumb{
    background-color: rgb(150, 52, 52);
}